body {
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
}

.calculator {
  width: 150px;
  height: 300px;
  border: 3px solid black;
  width: 50vh;
  border-radius: 20px;
  padding: 40px;
  background-color: red;
}
.main {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  padding: 15px;
  background-color: blue;
  font-size: 24px;
  font-weight: bold;
  border-radius: 9px;
  border: 2px solid;
}
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}
.display {
  height: 40px;
  border-radius: 5px;
  font-size: 17px;
  font-weight: bold;
  text-align: right;
  margin-bottom: 10px;
  border: 2px solid #000000;
  background: white;
  color: black;
}
.input {
  margin-left: 3px;
}
.back {
  width: 60px;
  height: 46px;
  background-color: #7bf3e9;
  border-radius: 5px;
  color: rgb(0, 0, 0);
  font-size: 17px;
  font-weight: bold;
  border: 2px solid black;
}
